@font-face {
  font-family: 'euclid';
  font-weight: 300;
  src: url(/static/media/EuclidCircularB-Regular-WebXL.2d75957d.woff2) format('woff2'), 
       url(/static/media/EuclidCircularB-Regular-WebXL.22b35d70.woff) format('woff'); /* Pretty Modern Browsers */
}

@font-face {
  font-family: 'euclid';
  font-weight: 700;
  src: url(/static/media/EuclidCircularB-Semibold-WebXL.0f28c449.woff2) format('woff2'), 
       url(/static/media/EuclidCircularB-Semibold-WebXL.b897c443.woff) format('woff'); /* Pretty Modern Browsers */
}

body {
  margin: 0;
  padding: 0;
  font-family: "euclid", -apple-system, BlinkMacSystemFont, "Arial", sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.website {
  padding: 25px 50px 0;
  width: 100%;
  max-width: 1000px; }

.navbar-wrapper .navbar {
  padding: 0 !important; }
  .navbar-wrapper .navbar .logo {
    font-weight: 700;
    font-size: 1.4em; }
    @media screen and (max-width: 576px) {
      .navbar-wrapper .navbar .logo {
        font-size: 1.1em; } }
  .navbar-wrapper .navbar .nav-link {
    padding-right: 0 !important;
    padding-left: 25px !important;
    font-size: 1.0em;
    text-align: center;
    font-weight: 300;
    color: black; }
    .navbar-wrapper .navbar .nav-link:hover {
      color: black; }
    @media screen and (max-width: 576px) {
      .navbar-wrapper .navbar .nav-link {
        font-size: 0.8em; } }
  .navbar-wrapper .navbar .navbar-toggler {
    border: none !important;
    padding: 0 !important;
    font-size: 14px !important;
    outline: 0 !important; }
  @media screen and (max-width: 576px) {
    .navbar-wrapper .navbar .nav-link {
      padding-left: 0px !important; } }

.welcome-message {
  margin-top: 160px;
  margin-bottom: 70px;
  font-weight: 700;
  font-size: 2.7em;
  line-height: 1em;
  letter-spacing: -0.06em; }
  .welcome-message #wave {
    font-size: 40px;
    animation-name: wave-animation;
    animation-duration: 2.5s;
    animation-iteration-count: infinite;
    transform-origin: 70% 70%;
    display: inline-block; }
  .welcome-message #check-out-message {
    color: #A7A7A7; }

@keyframes wave-animation {
  0% {
    transform: rotate(0deg); }
  10% {
    transform: rotate(-15deg); }
  20% {
    transform: rotate(12deg); }
  30% {
    transform: rotate(-15deg); }
  40% {
    transform: rotate(9deg); }
  50% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(0deg); } }
  @media screen and (max-width: 576px) {
    .welcome-message {
      font-size: 2.0em; } }

.tiled-section {
  padding-top: 50px;
  padding-bottom: 40px;
  font-size: 1.3em;
  margin-bottom: 30px; }
  @media screen and (max-width: 576px) {
    .tiled-section {
      font-size: 1.1em; } }
  .tiled-section .section-title {
    min-width: 200px;
    padding: 0;
    margin-left: 15px;
    margin-right: 15px;
    font-weight: 700; }
  .tiled-section .tile {
    text-align: center;
    margin-top: 40px; }
    .tiled-section .tile .tile-button {
      font-weight: 300;
      border: none;
      background-color: inherit;
      outline: 0 !important;
      cursor: pointer; }

.details-modal {
  background-color: white; }
  .details-modal .modal-dialog {
    max-width: 1000px;
    transition: none !important;
    margin: none !important; }
    .details-modal .modal-dialog .modal-content {
      border: none; }
      .details-modal .modal-dialog .modal-content .modal-header {
        border: none; }
  .details-modal h1 {
    font-size: 1.2em;
    font-weight: 700; }
  .details-modal h2 {
    font-size: 1.0em;
    font-weight: 300; }
  .details-modal .page-contents {
    margin-top: 10px;
    margin-bottom: 100px; }
    .details-modal .page-contents .page-title {
      font-size: 2em;
      font-weight: 700; }
    .details-modal .page-contents .close-button-wrapper {
      text-align: right;
      margin-top: 4px;
      padding: 0; }
      .details-modal .page-contents .close-button-wrapper .close-button {
        border: none;
        background-color: inherit;
        outline: 0 !important;
        cursor: pointer; }
    .details-modal .page-contents .experience-heading {
      padding-top: 70px; }
      .details-modal .page-contents .experience-heading .title, .details-modal .page-contents .experience-heading .date {
        font-size: 1.2em;
        font-weight: 700; }
      .details-modal .page-contents .experience-heading .date {
        text-align: right;
        float: right;
        color: #A9A9A9; }
    .details-modal .page-contents .location {
      font-size: 1.2em;
      margin-top: -10px;
      font-weight: 700;
      color: #A9A9A9; }
    .details-modal .page-contents .workDescription {
      padding-top: 20px; }
    .details-modal .page-contents .subsection {
      margin-left: 15px;
      margin-right: 15px;
      padding-top: 50px;
      width: 100%; }
    .details-modal .page-contents .notice {
      font-size: 0.9em;
      margin: 0 15px 0;
      padding: 5px 15px 5px;
      background-color: #FCE6E6;
      width: 100%; }
    .details-modal .page-contents .technologies {
      width: 100%; }
      .details-modal .page-contents .technologies .technologyPillContainer {
        display: flex;
        -moz-flex-direction: row;
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: wrap; }
        .details-modal .page-contents .technologies .technologyPillContainer .technologyPill {
          background-color: #F4F4F4;
          font-size: 0.8em;
          margin: 5px;
          border-radius: 500px;
          padding: 5px 10px 5px; }
    .details-modal .page-contents .view .viewButtonContainer .pillButton {
      background-color: #F4F4F4;
      font-size: 0.8em;
      margin: 5px;
      border-radius: 500px;
      padding: 5px 10px 5px;
      outline: none;
      border: 0;
      width: 150px;
      cursor: pointer; }
      .details-modal .page-contents .view .viewButtonContainer .pillButton:hover {
        background-color: black;
        color: white; }
    @media screen and (max-width: 580px) {
      .details-modal .page-contents {
        margin-top: 10px;
        position: absolute;
        top: 0;
        left: 0; }
        .details-modal .page-contents .page-title {
          font-size: 22px; }
        .details-modal .page-contents .experience-heading .title {
          font-size: 14px; }
        .details-modal .page-contents .experience-heading .date {
          font-size: 13px; }
        .details-modal .page-contents .location {
          font-size: 13px;
          margin-top: -5px; }
        .details-modal .page-contents .workDescription {
          padding-top: 15px; }
          .details-modal .page-contents .workDescription h2 {
            font-size: 14px; } }

.modal-backdrop {
  opacity: 0 !important; }

